.form {
  display: flex;
  flex-direction: column;
}

.formName,
.formNumber {
  display: flex;
  flex-direction: column;
  margin: 0.5rem auto;
  align-items: center;
  font-size: large;
  text-align: left;
  padding: 0.5rem 15rem 0.5rem 1rem;
}

.formOnbtn {
  height: 42px;
  width: 200px;
  background: black;
  border-radius: 25px;
  color: #ffff;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  border: none;
  cursor: pointer;
  position: relative;


}